"use client";

import { useEffect, useState } from "react";
import { Good } from "@/assets/svg/svg_icon";
import { X } from "lucide-react";
import recentPurchases from "../data/purchase.data";

const AUTO_SLIDE_INTERVAL = 4000; // 4s like most carousels

const RecentPurchaseTracking = () => {
    const [activeIndex, setActiveIndex] = useState(0);

    const activePurchase = recentPurchases[activeIndex];
    const { steps, currentStep } = activePurchase;

    /* ---------------- AUTO SLIDE (NO CLICKING) ---------------- */
    useEffect(() => {
        const interval = setInterval(() => {
            setActiveIndex((prev) =>
                prev === recentPurchases.length - 1 ? 0 : prev + 1
            );
        }, AUTO_SLIDE_INTERVAL);

        return () => clearInterval(interval);
    }, []);

    /* ---------------- PROGRESS WIDTH (CENTER OF STEP) ---------------- */
    const progressWidth =
        ((currentStep + 0.5) / steps.length) * 100;

    return (
        <div className="bg-white rounded-[20px] p-5 border border-gray-200 relative">
            {/* Gradient Definition */}
            <svg width="0" height="0" className="absolute pointer-events-none">
                <defs>
                    <linearGradient
                        id="closeIconGradient"
                        x1="0%"
                        y1="0%"
                        x2="100%"
                        y2="0%"
                    >
                        <stop offset="0%" stopColor="#E26E00" />
                        <stop offset="100%" stopColor="#FDA106" />
                    </linearGradient>
                </defs>
            </svg>

            {/* Header */}
            <div className="flex justify-between items-start mb-6">
                <div>
                    <h2 className="text-xl font-semibold text-gray-900 mb-2">
                        Recent purchase tracking
                    </h2>

                    <div className="flex items-center gap-3">
                        <div className="w-16 h-16 bg-gray-200 rounded-lg" />
                        <div>
                            <p className="text-sm text-gray-700 font-medium">
                                {activePurchase.productName}
                            </p>
                            <p className="text-sm text-gray-700">
                                Order ID: #{activePurchase.id}
                            </p>
                        </div>
                    </div>
                </div>

                <button className="cursor-pointer hover:opacity-80">
                    <X stroke="url(#closeIconGradient)" size={24} />
                </button>
            </div>

            {/* Estimated Arrival */}
            <div className="mb-8">
                <div className="flex items-center gap-2 mb-1">
                    <svg
                        className="w-4 h-4 text-gray-600"
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                    >
                        <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth={2}
                            d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4"
                        />
                    </svg>
                    <p className="text-sm text-gray-600">Estimated arrival</p>
                </div>

                <p className="text-lg font-semibold text-gray-900">
                    {activePurchase.estimatedArrival}
                </p>
            </div>

            {/* ---------------- PROGRESS TRACKER ---------------- */}
            <div className="relative">
                {/* Line (perfectly centered with steps) */}
                <div className="absolute left-0 right-0 top-[15px] h-1.5 bg-gray-200">
                    <div
                        className="h-full bg-[#077D58] transition-all duration-500"
                        style={{ width: `${progressWidth}%` }}
                    />
                </div>

                {/* Steps */}
                <div className="relative flex w-full">
                    {steps.map((step, index) => (
                        <div
                            key={index}
                            className="flex flex-col items-center flex-1"
                        >
                            {/* Step Icon */}
                            <div className="mb-1 z-10">
                                {step.completed ? (
                                    <div className="w-6 h-6 rounded-[6px] bg-[#003625] border border-[#003625] flex items-center justify-center">
                                        <Good />
                                    </div>
                                ) : (
                                    <div className="w-6 h-6 rounded-[6px] bg-gray-300 border border-white flex items-center justify-center">
                                        <Good />
                                    </div>
                                )}
                            </div>

                            {/* Label */}
                            <span
                                className={`text-sm whitespace-nowrap ${step.completed
                                    ? "text-[#077D58] font-medium"
                                    : "text-gray-500"
                                    }`}
                            >
                                {step.label}
                            </span>
                        </div>
                    ))}
                </div>
            </div>

            {/* ---------------- PAGINATION (INDICATOR ONLY) ---------------- */}
            <div className="flex items-center justify-center gap-4 pt-2">
                {recentPurchases.map((_, index) =>
                    index === activeIndex ? (
                        <div
                            key={index}
                            className="w-6 h-6 rounded-full border-2 border-bg-primary-color flex items-center justify-center"
                        >
                            <div className="w-3 h-3 rounded-full bg-gradient" />
                        </div>
                    ) : (
                        <div
                            key={index}
                            className="w-3 h-3 rounded-full bg-primary-color opacity-50"
                        />
                    )
                )}
            </div>
        </div>
    );
};

export default RecentPurchaseTracking;
